<template>
  <el-form
    :model="form"
    status-icon
    :rules="rules"
    ref="form"
    label-width="100px"
    class="login-container"
  >
    <h3 class="login_title">系统登录</h3>
    <el-form-item
      label="用户名"
      label-width="80px"
      prop="username"
      class="username"
    >
      <el-input
        type="input"
        v-model="form.username"
        autocomplete="off"
        placeholder="请输入账号"
      ></el-input>
    </el-form-item>
    <el-form-item label="密码" label-width="80px" prop="password">
      <el-input
        type="password"
        v-model="form.password"
        autocomplete="off"
        placeholder="请输入密码"
      ></el-input>
    </el-form-item>
    <el-form-item class="login_submit">
      <el-button type="primary" @click="login" class="login_submit"
        >登录</el-button
      >
    </el-form-item>
  </el-form>
</template>
<script>
// import Mock from 'mockjs'
import { getMenu } from "../../api/data";
export default {
  //登录方法
  name: "login",
  data() {
    return {
      form: {},
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" },
          {
            min: 3,
            message: "用户名长度不能小于3位",
            trigger: "blur",
          },
        ],
        password: [{ required: true, message: "请输入密码", trigger: "blur" }],
      },
      menu: [],
    };
  },
  methods: {
    login() {
      this.$http
        .post("courseSystem/auth/login", {
          username: this.form.username,
          password: this.form.password,
        })
        .then(({ data }) => {
          console.log(data);
          // 遍历一下菜单 改变他的属性
          data.menu.map((o) => {
            if (o.children.length == 0) {
              //说明没有孩子
              console.log(o.path);
              this.menu.push({
                path: o.path,
                name: o.name,
                label: o.label,
                icon: o.icon,
                url: o.url,
              });
            } else {
              this.menu.push(o);
            }
          });
          this.$store.commit("clearMenu");
          //将menu保存到token中
          this.$store.commit("setMenu", this.menu);
          this.$store.commit("setToken", data.token);
          this.$store.commit("addMenu", this.$router);
          this.$store.commit("setUserInfo", data.userInfo);
          this.$router.push({ name: "home" });
        });
    },
  },
};
</script>
<style lang="less" scoped>
.login-container {
  border-radius: 15px;
  background-clip: padding-box;
  margin: 180px auto;
  width: 350px;
  padding: 35px 35px 15px 35px;
  background-color: #fff;
  border: 1px solid #eaeaea;
  box-shadow: 0 0 25px #cac6c6;
}

.login_title {
  margin: 0px auto 40px auto;
  text-align: center;
  color: #505458;
}
.login_submit {
  margin: 10px auto 0px auto;
}
</style>